import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { IndexService } from 'src/app/Shared/index.service';
import { ModalController } from '@ionic/angular';
import { EditquestionComponent } from '../../Components/editquestion/editquestion.component';

@Component({
  selector: 'app-levels',
  templateUrl: './levels.page.html',
  styleUrls: ['./levels.page.scss'],
})
export class LevelsPage implements OnInit {
  id: any;
  tablestyle = 'bootstrap';
  qnA: any[];
  qdata: any;
  constructor(
    private activatedRoute: ActivatedRoute,
    private indexser: IndexService,
    public modalctrl: ModalController
  ) {}

  ngOnInit() {
    this.id = this.activatedRoute.snapshot.paramMap.get('id');
    console.log(this.id);
    this.getQuestionsData(this.id);
  }

  // ionViewWillEnter() {
  //   this.id = this.activatedRoute.snapshot.paramMap.get('id');
  //   console.log(this.id);
  // }

  getQuestionsData(id: string) {
    this.indexser.getQuestionsData(id).subscribe(
      (res: any) => {
        this.qnA = res.record[0].QnA;
        console.log(this.qnA);
        this.qdata = res.record[0];
      },
      (err) => {
        console.log(err);
      }
    );
  }

  delete(qid: string) {
    // console.log(this.qdata);
    console.log('delete');
    this.indexser.deleteoneQue(qid).subscribe(
      (res) => {
        console.log(res);
        this.getQuestionsData(this.id);
      },
      (err) => {
        console.log(err);
      }
    );
  }

  async edit(qid: string) {
    let ques = this.qnA.filter((el) => {
      return el._id == qid;
    });
    console.log(ques);

    const modal = await this.modalctrl.create({
      component: EditquestionComponent,
      cssClass: 'editqueModal',
      componentProps: {
        data: ques,
      },
    });
    return await modal.present();
  }
}
